.ptr-screen-viewport {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative; }

.ptr-screen-container {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  transition: left .3s ease-in-out; }

.ptr-screen-container > div {
  height: 100%;
  position: relative; }

.ptr-login-overlay {
  position: absolute;
  background: rgba(30, 30, 30, 0.95);
  color: #ccc;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100000;
  height: 100%;
  width: 100%;
  opacity: 0;
  pointer-events: none;
  transition: opacity .3s ease-in-out; }

.ptr-login-overlay.open {
  opacity: 1;
  pointer-events: all; }

.ptr-login {
  width: 20rem;
  flex-grow: 0; }

.ptr-login > div {
  margin-bottom: .75rem; }

.ptr-login .ptr-button {
  margin-right: .5rem; }

.ptr-user {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 2.5rem; }

.ptr-user > div {
  flex-grow: 0; }

.ptr-user-options {
  display: flex;
  align-items: center; }

.ptr-user-name, .ptr-user-login {
  display: flex;
  height: 2rem;
  padding: .25rem;
  font-size: 1rem;
  white-space: nowrap;
  overflow: hidden; }

.ptr-user-login {
  height: 2.5rem;
  display: flex;
  align-items: center; }

.ptr-user-login {
  align-items: center; }

.ptr-user-login > a {
  cursor: pointer; }

.ptr-screens {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  display: flex; }

.ptr-screen {
  position: relative;
  background: #c7c5c2;
  transition: width .5s ease-in-out;
  box-shadow: -5px 0 15px -5px rgba(0, 0, 0, 0.6);
  overflow-x: hidden; }

.ptr-screen.base {
  flex-grow: 1; }

.ptr-screen.focused {
  outline: none; }

.ptr-screen p {
  position: absolute;
  bottom: 0; }

/* Overlay */
.ptr-screen-overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: transparent;
  pointer-events: none;
  transition: background .2s ease-in-out, box-shadow .2s ease-in-out .3s; }

.ptr-screen.focused .ptr-screen-overlay {
  outline: none;
  box-shadow: inset 0 0.75rem 0 -0.25rem rgba(var(--accent6rgb), 0.6); }

.ptr-screen.disabled > .ptr-screen-overlay {
  background: rgba(129, 128, 125, 0.5);
  pointer-events: all;
  cursor: pointer; }

.ptr-screen.disabled > .ptr-screen-overlay:hover {
  background: rgba(152, 150, 148, 0.5); }

.ptr-screen.disabled > .ptr-screen-scroll {
  filter: blur(1px); }

/* Controls */
.ptr-screen-controls {
  position: absolute;
  cursor: pointer; }

.ptr-screen-controls.top {
  top: 1rem; }

.ptr-screen-controls.middle {
  top: 50%; }

.ptr-screen-controls > .ptr-button {
  color: var(--base5); }

.ptr-screen-controls > .ptr-button.ghost {
  border-color: var(--base2);
  transition: border-color .3s ease-in-out, color .3s ease-in-out; }

.ptr-screen.disabled .ptr-screen-controls > .ptr-button {
  color: var(--base6); }

.ptr-screen.disabled .ptr-screen-controls > .ptr-button.ghost {
  border-color: var(--base4); }

.ptr-screen-controls > .ptr-button:hover,
.ptr-screen.disabled .ptr-screen-controls > .ptr-button:hover {
  color: var(--base7); }

.ptr-screen-controls > .ptr-button.ghost:hover,
.ptr-screen.disabled .ptr-screen-controls > .ptr-button.ghost:hover {
  background: none;
  border-color: var(--base5); }

/* Scroll */
.ptr-screen-scroll {
  overflow-x: hidden;
  width: 100%;
  height: 100%;
  outline: none; }

.ptr-screen-scroll > div {
  height: 100%; }

.ptr-screen-content {
  padding: 0 2rem; }

.ptr-screen-scroll::-webkit-scrollbar {
  width: .9375rem; }

.ptr-screen-scroll::-webkit-scrollbar-button {
  height: .9375rem;
  background: rgba(0, 0, 0, 0.17); }

.ptr-screen-scroll::-webkit-scrollbar-button:vertical:decrement {
  /* up */
  background-image: url(./assets/a45a7a03.svg);
  background-size: contain; }

.ptr-screen-scroll::-webkit-scrollbar-button:vertical:increment {
  /* down */
  background-image: url(./assets/b706a53c.svg);
  background-size: contain; }

.ptr-screen-scroll::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1); }

.ptr-screen-scroll::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.15); }

.ptr-screen-scroll::-webkit-scrollbar-thumb:hover, .ptr-screen-scroll::-webkit-scrollbar-button:hover {
  background: rgba(60, 30, 0, 0.2); }

.ptr-screen-scroll::-webkit-scrollbar-thumb:active, .ptr-screen-scroll::-webkit-scrollbar-button:active {
  background: rgba(40, 35, 20, 0.5); }

.ptr-screen-scroll::-webkit-scrollbar-thumb:window-inactive {
  background: rgba(0, 0, 0, 0.075); }

.ptr-windows-container {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  z-index: 10; }

.ptr-window {
  background: var(--base0);
  border-top: 1px solid var(--base20);
  border-radius: .2rem;
  box-shadow: 0 7px 10px 0 rgba(var(--base100rgb), 0.25);
  z-index: 9999; }

.ptr-window-handle {
  cursor: move; }

.ptr-window-header {
  display: flex;
  height: 2.5rem;
  border-bottom: 1px solid var(--base25); }

.ptr-window-title {
  display: flex;
  align-items: center;
  white-space: nowrap;
  color: var(--base75);
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 1rem;
  font-weight: bold;
  flex-grow: 1;
  padding: .5rem .3rem .5rem .6rem; }

.ptr-window-title > .ptr-icon {
  fill: currentColor;
  width: 1.25rem;
  height: 1.25rem;
  margin-right: .35rem; }

.ptr-window-controls {
  height: 2.5rem;
  padding: calc(.375rem - 1px) 0.3rem; }

.ptr-window-controls.fixed {
  position: fixed;
  right: calc(.15rem + 16px);
  top: .15rem; }

.ptr-window-controls.fixed .ptr-window-control {
  background: rgba(var(--base100rgb), 0.1);
  border-radius: .75rem;
  margin-left: .3rem;
  transition: background .3s ease-in-out; }

.ptr-window-controls.fixed .ptr-window-control:hover {
  background: rgba(var(--base100rgb), 0.2); }

.ptr-window-control {
  cursor: pointer;
  height: 1.75rem;
  width: 1.75rem;
  padding: .25rem;
  display: flex;
  justify-content: center;
  align-items: center; }

.ptr-window-control .ptr-icon {
  height: 1.25rem;
  width: 1.25rem; }

.ptr-window-control .ptr-icon .line {
  fill: var(--base40);
  transition: fill .3s ease-in-out; }

.ptr-window-control:hover .ptr-icon .line {
  fill: var(--base80); }

.ptr-window-controls {
  display: flex; }

.ptr-window-content {
  overflow: auto;
  height: calc(100% - 2.5rem); }
