.ptr-map-grid {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  flex-wrap: wrap;
}

.ptr-map-grid .ptr-map {
  position: relative;
}

.ptr-map-grid :not(.row1) > .ptr-map {
  border-top: 1px solid #888;
}
.ptr-map-grid :not(.col1) > .ptr-map {
  border-left: 1px solid #888;
}

.ptr-map-wrapper {
  overflow: hidden;
  position: relative;
}
.ptr-leaflet-map {
  width: 100%;
  height: 100%;
  /* create stacking context */
  isolation: isolate;
  position: relative;
  z-index: 1;
}
.ptr-go-to-place-box {
  width: 12rem;
}

.ptr-go-to-place-box > .ptr-input-text {
  border: 1px solid var(--accent55);
}

.ptr-go-to-place-box > .ptr-input-text input::placeholder {
  color: rgba(var(--base50rgb), 0.8);
}

.ptr-go-to-place-box > .ptr-input-text > .ptr-icon {
  width: 1.25rem;
  margin-right: 0.25rem;
}

.ptr-go-to-place-box > .ptr-input-text > .ptr-icon .line {
  fill: var(--accent55);
}

.ptr-go-to-place-box > .ptr-input-text.focus > .ptr-icon .line {
  fill: var(--accent55);
}
.ptr-map-controls {
  position: absolute;
  bottom: 0.5rem;
  right: 0.5rem;
  z-index: 999;
  display: flex;
  flex-direction: column;
}

.ptr-map-controls .control {
  background: rgba(255, 255, 255, 0.8);
  border-radius: 0.3rem;
  z-index: 15;
  box-shadow: 0 0 0.3rem 0 rgba(0, 0, 0, 0.3);
  margin-bottom: 0.5rem;
}

.ptr-map-controls .control:last-child {
  margin-bottom: 0;
}

.ptr-map-controls.small {
  bottom: 13px;
}

.ptr-map-controls.small .control {
  margin-bottom: 5px;
}

.ptr-map-controls .control > button:focus,
.ptr-map-controls .menu-control > button:focus {
  outline: none;
}

.ptr-map-controls .control > button,
.ptr-map-controls .menu-control > button {
  padding: 0;
  background: none;
  border: 0;
  user-select: none;
  display: block;
  width: 2rem;
  height: 2rem;
  text-align: center;
  font-size: 1.125rem;
  color: var(--base50);
  cursor: pointer;
  transition: color 0.2s ease-in-out, background 0.2s ease-in-out;
}

.ptr-map-controls .control .hold-button {
  display: flex;
  align-items: center;
  justify-content: center;
}

.ptr-map-controls .control .ptr-icon {
  width: 1.35rem;
  height: 1.35rem;
}

.ptr-map-controls .legend-control .ptr-icon {
  width: 1.6rem;
  height: 1.6rem;
}

.ptr-map-controls .legend-control.open .ptr-icon {
  color: var(--accent50);
}

.ptr-map-controls.small .control > button {
  width: 1.6rem;
  height: 1.6rem;
  font-size: 0.975rem;
}

.ptr-map-controls .control > button:not(:first-child) {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.ptr-map-controls .control > button:first-child {
  border-top-right-radius: 0.3rem;
  border-top-left-radius: 0.3rem;
}

.ptr-map-controls .control > button:last-child {
  border-bottom-right-radius: 0.3rem;
  border-bottom-left-radius: 0.3rem;
}

.ptr-map-controls .control > button:hover,
.ptr-map-controls .menu-control > button:hover {
  background-color: white;
  color: var(--accent50);
}

.ptr-map-controls .hold-button .ptr-icon .line {
  transition: fill 0.2s ease-in-out;
}

.ptr-map-controls .hold-button:hover .ptr-icon .line {
  fill: var(--accent50);
}

.ptr-map-controls button > .ptr-icon .line {
  fill: currentColor;
}

.ptr-map-controls button.disabled .ptr-icon .line {
  color: var(--base35);
}

.ptr-map-controls .rotate-needle-control i {
  /*transition: all .2s ease-in-out;*/
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

/*#rotate-needle-control:hover i {*/
/*transform: rotate(-45deg);*/
/*}*/
.ptr-map-controls .tilt-control button {
  /*	overflow: hidden;*/
  background-position: center center;
  background-size: 1.2rem;
  background-repeat: no-repeat;
}
